import React from 'react';
// import PropTypes from 'prop-types';
import loginActions from '../../redux/actions/LoginActions'
import logo from '../../assets/img/logo.svg'
import './Login.scss'
import validator from 'validator';
import { notify } from '../../helpers/notifications'

class Login extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      loginForm: {
        email: "",
        password: ""
      },
      signUpForm: {
        signup_nombre: "",
        signup_email: "",
        signup_password: "",
        signup_password_repeate: ""
      }
    }

    this.login = this.login.bind(this)
    this.signup = this.signup.bind(this)

  }

  login() {
    const { email, password } = this.state.loginForm
    const { dispatch } = this.props
    dispatch(loginActions.login(email, password))
  }

  signup() {
    let { t } = this.props;
    const { signup_nombre, signup_email, signup_password, signup_password_repeate } = this.state.signUpForm

    if (validator.isEmpty(signup_nombre)) {
      notify(t('login.signUp.PHName') + " " + t('common.forms.Required'))
      return
    }

    if (!validator.isEmail(signup_email)) {
      notify(t('common.forms.InvalidEmail'))
      return
    }

    if (validator.isEmpty(signup_password)) {
      notify(t('login.common.PHPass') + " " + t('common.forms.Required'))
      return
    }

    if (validator.isEmpty(signup_password_repeate)) {
      notify(t('login.signUp.PHPassConfirm') + " " + t('common.forms.Required'))
      return
    }

    if (signup_password != signup_password_repeate) {
      notify(t('common.forms.PassConfirm'))     
      return
    }

    const { dispatch } = this.props
    dispatch(loginActions.signup(this.state.signUpForm))
  }

  updateLoginForm = (e) => {
    const { loginForm } = this.state;

    loginForm[e.target.name] = e.target.value;
    this.setState({ loginForm });
  }

  updateSignUpForm = (e) => {
    const { signUpForm } = this.state;

    signUpForm[e.target.name] = e.target.value;
    this.setState({ signUpForm });
  }

  handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      this.login()
    }
  }

  render() {
    let { t } = this.props;
    return (
      <div className="w-800 container my-5">
        <div className="row align-items-center mb-4">
          <img className="logo img-fluid mx-auto" src={logo} alt="" />
        </div>
        <div className="row">
          <fieldset disabled={this.props.isSubmitting} className="col-md-6 pr-3 pl-md-5 mt-5 border_r">
            <div className="mpb42"> {t('login.signIn.Title')} </div>
            <div className="mpr17 mb-3"> {t('login.signIn.Welcome')} </div>
            <div className="pmho_input mb-2">
              <input type="text" name="email" id="email" placeholder={t('login.signIn.PHUser')} onKeyPress={this.handleKeyPress} onChange={this.updateLoginForm} />
            </div>
            <div className="pmho_input mb-4">
              <input type="password" name="password" id="password" placeholder={t('login.common.PHPass')} onKeyPress={this.handleKeyPress} onChange={this.updateLoginForm} />
            </div>
            <div className="pmho_btn1">
              <button className="waves-effect" onClick={this.login}>{t('login.signIn.In')}</button>
            </div>
          </fieldset>
          <fieldset disabled={this.props.isSubmitting} className="col-md-6 pr-3 pl-md-5 mt-5 ">
            <div className="mpb42"> {t('login.signUp.SignUp')} </div>
            <div className="mpr17 mb-2"> {t('login.signUp.NewAccount')} </div>
            <div className="row">
              <div className="col-md-12 mb-2">
                <div className="pmho_input">
                  <input type="text" name="signup_nombre" id="signup_nombre" placeholder={t('login.signUp.PHName')} onChange={this.updateSignUpForm} />
                </div>
              </div>
              <div className="col-md-12 mb-2">
                <div className="pmho_input">
                  <input type="text" name="signup_email" id="signup_email" placeholder={t('login.signUp.PHMail')} onChange={this.updateSignUpForm} />
                </div>
              </div>

              <div className="col-md-12 mb-2">
                <div className="pmho_input">
                  <input type="password" name="signup_password" id="signup_password" placeholder={t('login.common.PHPass')} onChange={this.updateSignUpForm} />
                </div>
              </div>
              <div className="col-md-12 mb-2">
                <div className="pmho_input">
                  <input type="password" name="signup_password_repeate" id="signup_password_repeate" placeholder={t('login.signUp.PHPassConfirm')} onChange={this.updateSignUpForm} />
                </div>
              </div>
              <div className="col-md-12">
                <div className="pmho_btn1 mt-3">
                  <button href="#" className="waves-effect signup_ text-uppercase" onClick={this.signup}> { t('login.signUp.SignUp')} </button>
                </div>
              </div>
            </div>
          </fieldset>
        </div>
      </div>
    );
  }
}

export default Login;


